import {__} from '@wordpress/i18n';
import {
    PanelBody,
    BaseControl,
    SelectControl,
    RangeControl,
    TextControl,
    ToggleControl,
    Dashicon,
    Tip,
    __experimentalBoxControl as BoxControl,
}           from '@wordpress/components';
import {
    PanelColorSettings,
    InspectorControls,
    useBlockProps,
    InnerBlocks
}           from '@wordpress/block-editor';

export default function Edit(props) {
    const {attributes, setAttributes} = props;

    const {
        headingHTMLTag,
        tocAdded,
        desktopHeadingSize,
        tabletHeadingSize,
        mobileHeadingSize,
        headingColor,
        darkHeadingColor,
        initOpen,
        shadow,
        borderStyle,
        borderColor,
        darkBorderColor,
        borderWidth,
        borderRadius,
        background,
        darkBackground,
        desktopPadding,
        tabletPadding,
        mobilePadding,
    } = attributes;

    const setAttribute = (name, value) => {
        setAttributes({[name]: value});
    };

    const headingTagOptions = [
        {label: __('H2'), value: 'h2'},
        {label: __('H3'), value: 'h3'},
        {label: __('H4'), value: 'h4'},
        {label: __('H5'), value: 'h5'},
        {label: __('H6'), value: 'h6'},
        {label: __('p'), value: 'p'},
        {label: __('Div'), value: 'div'},
    ];

    const borderStyleOptions = [
        {label: __('None'), value: 'none'},
        {label: __('Solid'), value: 'solid'},
        {label: __('Dashed'), value: 'dashed'},
        {label: __('Dotted'), value: 'dotted'},
        {label: __('Double'), value: 'double'},
    ];

    let wrapperClassName = `gb-wrap gb-accordion`;

    if (shadow) {
        wrapperClassName += ` yes-shadow`;
    }

    if (initOpen) {
        wrapperClassName += ` yes-open`;
    }

    const blockProps = useBlockProps({
        className: wrapperClassName,
        style: {
            ...(headingColor && {'--heading-color': headingColor}),
            ...(darkHeadingColor && {'--dark-heading-color': darkHeadingColor}),
            ...(desktopHeadingSize && {'--desktop-heading-size': `${desktopHeadingSize}px`}),
            ...(tabletHeadingSize && {'--tablet-heading-size': `${tabletHeadingSize}px`}),
            ...(mobileHeadingSize && {'--mobile-heading-size': `${mobileHeadingSize}px`}),
            ...(borderStyle && {'--border-style': borderStyle}),
            ...(borderRadius && {'--border-radius': `${borderRadius}px`}),
            ...(borderWidth && {'--border-width': `${borderWidth.top || 0} ${borderWidth.right || 0} ${borderWidth.bottom || 0} ${borderWidth.left || 0}`}),
            ...(borderColor && {'--border-color': borderColor}),
            ...(darkBorderColor && {'--dark-border-color': darkBorderColor}),
            ...(background && {'--bg': background}),
            ...(darkBackground && {'--dark-bg': darkBackground}),
            ...(desktopPadding && {'--desktop-padding': `${desktopPadding.top || 0} ${desktopPadding.right || 0} ${desktopPadding.bottom || 0} ${desktopPadding.left || 0}`}),
            ...(tabletPadding && {'--tablet-padding': `${tabletPadding.top || 0} ${tabletPadding.right || 0} ${tabletPadding.bottom || 0} ${tabletPadding.left || 0}`}),
            ...(mobilePadding && {'--mobile-padding': `${mobilePadding.top || 0} ${mobilePadding.right || 0} ${mobilePadding.bottom || 0} ${mobilePadding.left || 0}`}),
        },
    });

    return (
        <div {...blockProps}>
            <InspectorControls>
                <PanelBody title={__('General')} initialOpen={true}>
                    <SelectControl
                        label={__('Heading Tag')}
                        value={headingHTMLTag}
                        options={headingTagOptions}
                        onChange={(value) => setAttribute('headingHTMLTag', value)}
                    />
                    <ToggleControl
                        label={__('Table of Content Included')}
                        checked={tocAdded}
                        onChange={(value) => setAttribute('tocAdded', value)}
                    />
                    <ToggleControl
                        label={__('Initial Open 1st Tab')}
                        checked={initOpen}
                        onChange={(value) => setAttribute('initOpen', value)}
                    />
                </PanelBody>
            </InspectorControls>
            <InspectorControls group="styles">
                <PanelBody title={__('Style')} initialOpen={false}>
                    <label className="rb-control-label">{__('Font Size')}</label>
                    <div className="responsive-settings">
                        <div>
                            <label>
                                <Dashicon icon="desktop"/>
                                {__('Desktop')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="18"
                                value={desktopHeadingSize}
                                onChange={(value) => setAttribute('desktopHeadingSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="tablet"/>
                                {__('Tablet')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="17"
                                value={tabletHeadingSize}
                                onChange={(value) => setAttribute('tabletHeadingSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="smartphone"/>
                                {__('Mobile')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="16"
                                value={mobileHeadingSize}
                                onChange={(value) => setAttribute('mobileHeadingSize', value)}
                            />
                        </div>
                    </div>
                    <PanelColorSettings
                        title={__('Color')}
                        colorSettings={[
                            {
                                value: headingColor,
                                onChange: (value) => setAttribute('headingColor', value),
                                label: __('Color'),
                            },
                            {
                                value: darkHeadingColor,
                                onChange: (value) => setAttribute('darkHeadingColor', value),
                                label: __('Dark Mode - Color'),
                            },
                        ]}
                    />
                </PanelBody>
                <PanelBody title={__('Border & Shadow')} initialOpen={false}>
                    <SelectControl
                        label={__('Border Style')}
                        value={borderStyle}
                        options={borderStyleOptions}
                        onChange={(value) => setAttribute('borderStyle', value)}
                        help={__('Please select the border color to see the border if you enable it.')}
                    />
                    {borderStyle && borderStyle !== 'none' && (
                        <>
                            <BoxControl
                                resetValues={{top: null, right: null, bottom: null, left: null}}
                                label={__('Border Width')}
                                values={borderWidth}
                                onChange={(value) => setAttribute('borderWidth', value)}
                            />
                            <PanelColorSettings
                                title={__('Border Color')}
                                colorSettings={[
                                    {
                                        value: borderColor,
                                        onChange: (value) => setAttribute('borderColor', value),
                                        label: __('Color'),
                                    },
                                    {
                                        value: darkBorderColor,
                                        onChange: (value) => setAttribute('darkBorderColor', value),
                                        label: __('Dark Mode - Color'),
                                    },
                                ]}
                            />
                        </>
                    )}
                    <RangeControl
                        label={__('Border Radius')}
                        value={borderRadius}
                        onChange={(value) => setAttribute('borderRadius', value)}
                        step={1}
                        max={100}
                        min={0}
                    />
                    <ToggleControl
                        label={__('Shadow')}
                        checked={shadow}
                        onChange={(value) => setAttribute('shadow', value)}
                    />
                    <PanelColorSettings
                        title={__('Background')}
                        colorSettings={[
                            {
                                value: background,
                                onChange: (value) => setAttribute('background', value),
                                label: __('Background'),
                            },
                            {
                                value: darkBackground,
                                onChange: (value) => setAttribute('darkBackground', value),
                                label: __('Dark Mode - Background'),
                            },
                        ]}
                    />
                </PanelBody>
                <PanelBody title={__('Inner Padding')} initialOpen={false}>
                    <BaseControl><Tip>{__('These settings will apply to block wrapper. Click outside to see the update!')}</Tip></BaseControl>
                    <div className="res-padding-control">
                        <BoxControl
                            resetValues={{top: '15px', right: '30px', bottom: '15px', left: '30px'}}
                            label={<><Dashicon icon="desktop"/> {__('Desktop')}</>}
                            values={desktopPadding}
                            onChange={(value) => setAttribute('desktopPadding', value)}
                        />
                        <BoxControl
                            resetValues={{top: '10px', right: '25px', bottom: '10px', left: '25px'}}
                            label={<><Dashicon icon="tablet"/> {__('Tablet')}</>}
                            values={tabletPadding}
                            onChange={(value) => setAttribute('tabletPadding', value)}
                        />
                        <BoxControl
                            resetValues={{top: '10px', right: '20px', bottom: '10px', left: '20px'}}
                            label={<><Dashicon icon="smartphone"/> {__('Mobile')}</>}
                            values={mobilePadding}
                            onChange={(value) => setAttribute('mobilePadding', value)}
                        />
                    </div>
                </PanelBody>
            </InspectorControls>
            <InnerBlocks
                allowedBlocks={['foxiz-elements/accordion-item']}
                templateLock={false}
                template={[['foxiz-elements/accordion-item', {}]]}
                renderAppender={InnerBlocks.ButtonBlockAppender}
                templateInsertUpdatesSelection={true}
                __experimentalConvertToMappable={true}
            />
        </div>
    )
}
